<template>
  <div class="container with-nav">
    <div class="card">
      <h1>Vue Component Api</h1>
      <div class="form-control">
        <input type="text" v-model="firstName">
        <div>{{inputValue}}</div>
        <button class="btn">Изменить</button>
      </div>
    </div>
    <framework-info @change-version="versionChange"/>
  </div>
</template>

<script>
import {
  ref,
  provide,
  computed,
  toRefs,
  onMounted,
  onBeforeMount,
  onUpdated,
  onBeforeUpdate,
} from 'vue'

import FrameworkInfo from '../components/FrameworkInfo.vue';

export default {
  components: { FrameworkInfo },
  setup() {
    const name = ref('VUE JS');
    const version = ref('2');
    const firstName = ref('');

    provide('name', name);
    provide('version', version);

    const inputValue = computed(() => {
      return firstName.value.length > 0 ? `Вы ввели: ${firstName.value}` : 'Вы еще ничего не ввели'; 
    })

    function versionChange(num) {
      version.value = num;
    };
    
    return {
      name, 
      version,
      firstName,
      versionChange,
      inputValue,
    }
  },

  components: {
    FrameworkInfo,
  }
}
</script>
